<template>
  <div>
    <div class="page-title"></div>
    <mt-field class="field" label="Telegram ID" v-model="userid" :value="userid" :attr="{maxlength: 10}">
      <mt-button type="default" size="small" @click.native="queryAccount"></mt-button>
    </mt-field>
    <div v-if="visible">
      <p v-if="!hasActions"></p>
      <div v-else>
        <div>
          <mt-cell
            v-for="(item, index) in result"
            :key=index
            :title=item
          >
          </mt-cell>
        </div>

        <div class="button-group" align="center">
          <mt-button size="small" @click.native="previousPage"></mt-button>
          &#12288;{{ page }}/{{ sum }}&#12288;
          <mt-button size="small" @click.native="nextPage"></mt-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import Utils from '@/scripts/utils'
import Methods from '@/services/methods'
import { Toast, Indicator } from 'mint-ui'

// 
const ReasonGive = 1
// 
const ReasonSystem = 2
// 
const ReasonReceive = 3
// 
const ReasonGiveBack = 4
// 
const ReasonDeposit = 5
// 
const ReasonWithdraw = 6
// 
const ReasonWithdrawSuccess = 7
// 
const ReasonWithdrawFailure = 8

export default {
  data () {
    return {
      sum: 1,
      page: 1,
      count: 10,
      userid: '',
      visible: false,
      result: []
    }
  },

  computed: {
    hasActions () {
      return this.result.length > 0
    }
  },

  methods: {
    // 
    async nextPage (event) {
      if (this.page < this.sum) {
        this.count++
        await this.queryAccount(undefined)
      }
    },

    // 
    async previousPage (event) {
      if (this.page > 1) {
        this.count--
        await this.queryAccount(undefined)
      }
    },

    // 
    formatActions (actions) {
      let result = []
      for (let i = 0; i < actions.length; i++) {
        const action = actions[i]
        const timestamp = moment.unix(action.timestamp).format('YYYY-MM-DD HH:mm:ss')
        switch (action.reason) {
          // 
          case ReasonGive:
            result.push(`* ${timestamp} ${action.ref_lucky_money_id},  ${action.locked} ${action.symbol}`)
            break
          // 
          case ReasonSystem:
            result.push(`* ${timestamp}  ${action.balance} ${action.symbol}`)
            break
          // 
          case ReasonReceive:
            result.push(`* ${timestamp}  ${action.ref_user_name}(${action.ref_user_id}) ${action.ref_lucky_money_id},  ${action.balance} ${action.symbol}`)
            break
          // 
          case ReasonGiveBack:
            result.push(`* ${timestamp} ${action.ref_lucky_money_id},  ${action.locked} ${action.symbol}`)
            break
          // 
          case ReasonDeposit:
            result.push(`* ${timestamp}  ${action.balance} ${action.symbol} , : ${action.ref_block_height}, TxID: ${action.ref_tx_id}`)
            break
          // 
          case ReasonWithdraw:
            result.push(`* ${timestamp} " ${action.locked} ${action.symbol} ${action.name} ${action.ref_address} ,  ${action.fee} ${action.symbol}`)
            break
          // 
          case ReasonWithdrawSuccess:
            result.push(`* ${timestamp} " ${Math.abs(action.locked)} ${action.symbol} ${action.name} ${action.ref_address} `)
            break
          // 
          case ReasonWithdrawFailure:
            result.push(`* ${timestamp} " ${Math.abs(action.locked)} ${action.symbol} ${action.name} ${action.ref_address} , TxID${action.ref_tx_id}`)
            break
        }
      }
      return result
    },

    // 
    async queryAccount (event) {
      this.visible = false
      if (!Utils.validateTelegramID(this.userid)) {
        Toast({
          message: 'Telegram ID ',
          position: 'top'
        })
        return
      }

      Indicator.open()
      let result = await Methods.getActions(
        this.userid, (this.page - 1) * this.count, this.count)
      Indicator.close()
      if (!result.ok) {
        Toast({
          message: '',
          position: 'top'
        })
        return
      }

      this.sum = parseInt(result.data.sum / this.count)
      if (this.sum === 0 && result.data.sum > 0) {
        this.sum = 1
      }
      this.visible = true
      this.result = this.formatActions(result.data.actions)
    }
  }
}
</script>

<style scoped>
  .field {
    background-color:gainsboro;
  }
  .button-group {
    margin-top: 10px
  }
</style>



